header{
    padding: 14px 26px;
    width: 100%;
    background-color: black;
    display: flex;
    align-items: center;
    position: relative;
    height: 56px;
}
.arrow{
    position: absolute;
    left: 24px;
    top: 16px;
    width: 24px;
    height: 24px;
    cursor: pointer;
}
.header_logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 16px;
    width :76.92px;
    height: auto;
}
.header_logo > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.like {
    position: absolute;
    right: 24px;
    top: 16px;
    width: 24px;
    height: 24px;
    overflow: hidden;
    cursor: pointer;
}
.like > img{
    width: 100%;
    height: 100%;
    object-fit: center;
}