.pocemonCard{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: auto;
    background: #F2F2F2;
    border-radius: 8px;
    border: 1px solid #F2F2F2;
    cursor: pointer;
}
.pocemonImg{
    width: 62%;
    height: auto;
    overflow: hidden;
    padding: 23px 0 24px;
}
.pocemonImg > img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.pocemonInfo{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background: #FFFFFF;
    border-radius: 0px 0px 8px 8px;
    padding: 8px 12px;
}
.pocemonName{
    text-transform: capitalize;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #201D2A;
}
.pocemonLike{
    width: 16px;
    height: 16px;
}